<template>
	<view class="ljs-turntable" :class="show?'show':'hide'">
		<view class="close" @click="close">
			<image class="ico" src="./images/ico_close.png"></image>
		</view>
		<view class="box">
		</view>
		<view class="box2">
		</view>
		<view class="but" @click="go">
			<image class="pic" src="./images/but.png"></image>
		</view>
